// 
// >=768 1200版心显示
@media screen and (min-width: 768px) {
  .App {
    .foot {
      padding-top: 150px;
      width: 1200px;
      // background-color: red;
      margin: 0 auto;
      padding-bottom: 30px;

      .foot-item {
        float: left;
        width: 270px;
        // background-color: palegoldenrod;

        .foot-item-title {
          font-size: 16px;
          font-weight: 500;
          color: rgba(7, 2, 55, 1);
          height: 36px;
        }

        .foot-item-content {
          font-size: 14px;
          font-weight: 400;
          color: rgba(148, 145, 170, 1);
          height: 26px;

          a {
            text-decoration: none;
            font-size: 14px;
            font-weight: 400;
            color: rgba(148, 145, 170, 1);
            transition: 0.3s;

            &:hover {
              color: #D5A24E;
            }
          }
        }

      }
    }
  }
}

// >=768 1200版心显示
@media screen and (max-width: 767px) {
  .App {
    .foot {
      padding-top: 50px;
      // width: 1200px;
      // background-color: red;
      width: 90%;
      margin: 0 auto;
      padding-bottom: 30px;

      .foot-item {
        // float: left;
        // width: 270px;
        // background-color: palegoldenrod;

        .foot-item-title {
          margin-top: 30px;
          font-size: 14px;
          font-weight: 300;
          color: rgba(7, 2, 55, 1);
          height: 30px;
        }

        .foot-item-content {
          font-size: 12px;
          font-weight: 200;
          color: rgba(148, 145, 170, 1);
          height: 26px;

          a {
            text-decoration: none;
            font-size: 12px;
            font-weight: 200;
            color: rgba(148, 145, 170, 1);
            transition: 0.3s;

            &:hover {
              color: #D5A24E;
            }
          }
        }

      }
    }
  }
}